.image-flipper {

    position: absolute;
    cursor: pointer;
    max-height: 200px;
    z-index: 4;
    justify-self: center;
    transform: translate(900px, 780px);
  }

  .image-flipper img {
    max-height: 200px;
    
    transform: translate(-50%, -50%);
    box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, 0.5);

  }

