.hobby-container-wrapper {
  display: flex;
  justify-content: center; 
  align-items: center;
  overflow: hidden; /* Hide overflow */
  height: 80vh; /* Full viewport height */
  position: relative; /* Needed for absolute positioning of the child */
}


.hobby-container {
  position: absolute;
  left: 50%;
  top: 35%;
  transform: translate(-50%, -50%);
  width: 1800px;
  height: 900px;
}

  
  .hobby-slice {
    position: absolute;
    width: 100%;
    height: 100%;
    transform-origin: 50% 50%;
    transition: clip-path 0.5s ease-in-out;
  }
  
  .hobby-icon {
    position: absolute;
    transform: translate(-50%, -50%);
    width: 144px;
    max-width: 144px;
    transition: left 0.5s ease-in-out, top 0.5s ease-in-out, transform 0.5s ease-in-out;
  }
  
  .hobby-slice.expanded .hobby-icon {
    transform: translate(-50%, -50%) scale(1.5);
    border-radius: 50%;
    box-shadow: 0px 0px 10px 5px rgba(255, 255, 255, 0.5);
  }
  
  
  /* Custom colors for each slice */
  
  .hobby-slice:nth-child(1) {
      clip-path: polygon(900px 450px, 900px 0px, 1800px 0px, 1800px 199.8px); 
      background-color: rgb(57, 57, 57);
  }
  .hobby-slice:nth-child(2) {
    clip-path: polygon(900px 450px, 1800px 199.8px, 1800px 199.8px, 1800px 900px, 1224px 900px); 
    background-color: rgb(67, 0, 0);
  }
  .hobby-slice:nth-child(3) { 
    clip-path: polygon(900px 450px, 1224px 900px, 572.4px 900px, 900px 450px);  
    background-color: rgb(71, 71, 0);  

  }  
  .hobby-slice:nth-child(4) {
    clip-path: polygon(900px 450px, 572.4px 900px, 0px 900px, 0px 199.8px, 0px 199.8px);
    background-color: rgb(10, 10, 115);
  }
  .hobby-slice:nth-child(5) { 
    clip-path: polygon(900px 450px, 0px 199.8px, 0px 0px, 900px 0px); 
    background-color: rgb(0, 81, 0);
  }

    
.hobby-slice:nth-child(1).expanded {
    clip-path: polygon(472.4px 900px, 472.4px 0px, 1800px 0px, 1800px 501.3px);
}
.hobby-slice:nth-child(2).expanded {
    clip-path: polygon(0px 199.8px, 1000px 0px, 1800px 0px, 1800px 900px, 850px 900px);
}
.hobby-slice:nth-child(3).expanded {
    clip-path: polygon(1250px 0px, 1553.4px 900px, 244.8px 900px, 550px 0px);
}
.hobby-slice:nth-child(4).expanded {
    clip-path: polygon(1800px 199.8px, 950px 900px, 0px 900px, 0px 0px, 800px 0px);
}
.hobby-slice:nth-child(5).expanded {
    clip-path: polygon(1324px 900px, 0px 501.3px, 0px 0px, 1325.8px 0px);
}

.hobby-slice:nth-child(1) .hobby-icon {
  left: 972px;
  top: 369px;
}

.hobby-slice:nth-child(2) .hobby-icon {
  left: 1026px;
  top: 495px;
}

.hobby-slice:nth-child(3) .hobby-icon {
  left: 900px;
  top: 585px;
}

.hobby-slice:nth-child(4) .hobby-icon {
  left: 774px;
  top: 495px;
}

.hobby-slice:nth-child(5) .hobby-icon {
  left: 828px;
  top: 369px;
}

.hobby-slice.expanded .hobby-icon {
  left: 900px;
  top: 135px;
}

  
  .hobby-slice.expanded .hobby-icon {
    left: 900px;
    top: 135px;
  }

  .hobby-title {
    position: absolute;
    transform: translateX(-50%);
    font-size: 1.2rem;
    font-weight: normal;
    color: white;
    opacity: 0;
    transition: opacity 0.3s ease, left 0.5s ease-in-out, top 0.5s ease-in-out;
    pointer-events: none; 
  }
  
  .hobby-slice:hover .hobby-title {
    opacity: 1;
    font-weight: bold;
  }
  
  .hobby-slice:nth-child(1) .hobby-title {
    left: 972px;
    top: 290px;
}

.hobby-slice:nth-child(2) .hobby-title {
    left: 1090px;
    top: 545px;
}

.hobby-slice:nth-child(3) .hobby-title {
    left: 900px;
    top: 645px;

}

.hobby-slice:nth-child(4) .hobby-title {
    left: 760px;
    top: 545px;
}

.hobby-slice:nth-child(5) .hobby-title {
    left: 828px;
    top: 290px;
}

  
  .hobby-slice:hover .hobby-title {
    opacity: 1;
    font-weight: bold;
  }

  .hobby-slice.expanded .hobby-title {
    left: 900px;
    top: 220px;
  }

  .hobby-description {
    position: absolute;
    font-size: .8rem;
    color: white;
    padding: 10px 0px;
    left: 520px;
    top: 250px;
    height: 350px;
    overflow-y: auto;
    min-width: 280px;
    max-width: 780px;

  }
  
  .hobby-description::-webkit-scrollbar {
    width: 10px; 
    background: transparent;
  }
  .hobby-description:hover::-webkit-scrollbar-thumb {
    background-color: rgb(179, 179, 179);
    border-radius: 5px;
  }
  
  .hobby-description:hover::-webkit-scrollbar-track {
    background: white;
    border-radius: 5px;
  }
  