.contact-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  
  .contact-form {
    margin: auto;
    width: 80%;
    padding: 20px;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input, textarea {
    width: 100%;
    padding: 10px;
  }

    textarea {
        height: 150px;
    }
  
  button {
    padding: 10px 20px;
    background-color: #4CAF50;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #45a049;
  }
  
  .contact-info ul {
    list-style: none;
    padding: 0;

  }

  .contact-section, .image-section {
    flex: 1;
    min-width: 300px;
  }

  .contact-section a:link {
    color: #CCCCCC; 
  }
  
  .contact-section a:hover {
    color: #E0E0E0; 
  }
  
  .contact-section a:active {
    color: #6699CC; 
  }
  
  .contact-section a:visited {
    color: #6699CC; 
  }
  



  .image-section img {
    height: 600px;
    display: block;
  }
  
  /* Media query for smaller screens */
  @media (max-width: 1200px) {
    .contact-container {
      flex-direction: column;
    }
  }