.scrolling-banner-container {
  width: 1750px;
  height: 230px;
  overflow-x: hidden;
  position: relative;
  margin: auto;
  top: 650px;
}


.scrolling-banner {
    position: absolute;
    top: 0;
    left: 0%;
    transform: translateX(-50%);
    width: 679%; 
    display: flex;
    overflow-x: hidden;
    white-space: nowrap;
    scroll-behavior: smooth;
    animation: scrollBanner 60s linear infinite;
    z-index: 4;
  }
  
  .banner-image {
    max-height: 15vh;
    margin: 5px;
    display: inline-block;
  }
  
  @keyframes scrollBanner {
    0% {
      transform: translateX(0%);
    }
    100% {
      transform: translateX(-50%);
    }
  }
  